<template>
  <div class="zhibei"
    :style="{'transform': `rotateZ(${angle}deg)`}"></div>
</template>

<script>
import * as util from "../assets/util";

export default {
  name: 'compass',
  data () {
    return {
      getIt: false,
      angle: 0
    }
  },
  methods: {
    deviceOrientationListener: function(event){
      //console.log(event.alpha)
      this.angle = event.alpha;
      
    }
  },
  created: function(){
    if (window.DeviceOrientationEvent) {
      let vm = this;
      window.addEventListener('deviceorientation', util.throttle(vm.deviceOrientationListener, 10, 30))
    } else {
      alert("Sorry your browser doesn't support Device Orientation")
    }
  },
  mounted: function(){
    
  }
}
</script>

